.container {
  position: fixed;
  right: calc(var(--padding-medium) + var(--horizontal-whitespace));
  bottom: var(--padding-medium);
  border: 1px solid var(--base-medium);
  background: var(--foreground);
  z-index: 99;
  border-radius: var(--border-radius);
  display: flex;
  flex-direction: column;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.166);

  .chatIconButton {
    color: var(--text-color);
    background: none;
    border: none;
    padding: none;
    transition: filter 100ms ease;
    position: relative;
    svg {
      width: 2rem;
      height: 2rem;
      filter: drop-shadow(0px 2px 3px rgba(0, 0, 0, 0.5)) opacity(0.5);
    }

    .notifications {
      border-radius: 50%;
      background: green;
      color: white;
      text-shadow: 0px 1px 2px black;
      font-weight: 600;
      font-size: var(--font-md);
      box-shadow: 0px 2px 3px black;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      z-index: 97;
      padding: 0 var(--padding);
    }
  }
  .chatIconButton:hover {
    svg {
      filter: drop-shadow(0px 2px 3px rgba(0, 0, 0, 0.5)) opacity(1);
    }
  }

  .topTray {
    display: flex;
    justify-content: space-between;
    padding: var(--padding-medium);
    border-radius: var(--border-radius-medium);
    color: grey;
    border-bottom: 1px solid var(--base-light);

    .icons {
      display: flex;
    }
  }

  .inner {
    display: flex;
    box-sizing: border-box;
    gap: var(--padding-medium);
    flex-grow: 1;
    padding: var(--padding-medium);
  }
}
