.inputLabelWrapper {
  display: flex;
  flex-direction: column;
  gap: var(--padding-small);
  width: 100%;
  position: relative;
  button {
    margin: 0;
  }
  label {
    padding: 0;
    margin: 0;
    line-height: 1;
  }
}

.resMsg,
.resMsgErr {
  text-align: center;
  justify-content: center;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--padding-medium);
}
.resMsgErr {
  color: red;
}

.loadingSpinner {
  font-size: var(--font-md);
  animation-name: spin;
  animation-duration: 700ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}

.errorTip {
  position: absolute;
  left: var(--padding);
  bottom: -1rem;
  background: red;
  text-align: left;
  padding: 0 var(--padding-medium);
  color: white;
  z-index: 99;
  filter: opacity(0.8);
  border-radius: var(--border-radius-medium);
}
